.card-container {
  display: flex;
  flex-direction: column;
  background-color: #d7e4ab;

  border-radius: 5px;
  padding: 25px;
  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  backface-visibility: hidden;
  transform: translateZ(0);
  transition: transform 0.25s ease-out;
  margin: 10px;
}

.card-container:hover {
  transform: scale(1.05);
}

h2 {
  color: #030329ea;
  font-size: 30px;
}
